body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 20px;
}

ol,
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

a {
    text-decoration: none;
    color: #333;
}

.App {
    width: 800px;
    margin: 0 auto;
}

.header {
    position: relative;
}

.header h1 {
    padding: 10px;
}

.header .menu {
    position: absolute;
    top: 10px;
    right: 10px;
}

.header .link {
    border: 1px solid #333;
    padding: 10px;
    margin-left: 10px;
    background-color: #efefef;
    font-weight: bold;
    border-radius: 4px;
}

.list_day {
    display: flex;
    flex-wrap: wrap;
}

.list_day li {
    flex: 20% 0 0;
    box-sizing: border-box;
    padding: 10px;
}

.list_day a {
    display: block;
    padding: 20px 0;
    font-weight: bold;
    color: #fff;
    text-align: center;
    border-radius: 10px;
    background-color: dodgerblue;
}

table {
    border-collapse: collapse;
    width: 100%;
}

table td {
    width: 25%;
    height: 70px;
    border: 1px solid #ccc;
    text-align: center;
    font-size: 26px;
}

table td:first-child {
    width: 10%;
}

.off td {
    background: #eee;
    color: #ccc;
}

.day {
    display: flex;
    justify-content: center;
    align-items: center;
}

.day .prev {
    margin: 10px;
    padding: 10px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    border: 0 none;
    border-radius: 6px;
    padding: 10px 20px;
    color: #fff;
    background-color: dodgerblue;
}

.day .next {
    margin: 10px;
    padding: 10px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    border: 0 none;
    border-radius: 6px;
    padding: 10px 20px;
    color: #fff;
    background-color: dodgerblue;
}

.btn_del {
    margin-left: 10px;
    color: #fff;
    background-color: firebrick;
}

button {
    padding: 10px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    border: 0 none;
    border-radius: 6px;
    padding: 10px 20px;
    color: #fff;
    background-color: dodgerblue;
}

.input_area {
    margin-bottom: 10px;
}

.input_area label {
    display: block;
    margin-bottom: 10px;
}

.input_area input {
    width: 400px;
    height: 40px;
    font-size: 20px;
    padding: 0 10px;
}

.input_area select {
    width: 400px;
    height: 40px;
    font-size: 20px;
}